export default class Utils {
    static isAppleMobile() {
        if ((['iPhone', 'iPad', 'iPod'].includes(navigator.platform) ||
            (navigator.userAgent.match(/Mac/) && navigator.maxTouchPoints && navigator.maxTouchPoints > 2))
            && ('serviceWorker' in navigator))
            return true;
        return false;
    }
    static isStandalone() {
        if (window.matchMedia('(display-mode: standalone)').matches || ('standalone' in navigator && navigator.standalone === true))
            return true;
        return false;
    }
    static async isRelatedAppsInstalled() {
        if ('getInstalledRelatedApps' in navigator)
            await navigator.getInstalledRelatedApps().then((relatedApps) => {
                return relatedApps.find((app) => app.id === 'com.learnpwa.app') ? true : false;
            });
        return false;
    }
}
